<template>
  <div class="detail"
       v-if="eveny">
    <navtab @jump="jump"></navtab>
    <betterScroll class="scroll1"
                  ref="scroll"
                  @scroll="scroll"
                  @pullingUp="pullingUp"
                  :probeType="3"
                  :pullUpLoad="true">
      <detailSwiper :topImages="topImages"></detailSwiper>
      <titleDetail :itemInfo="itemInfo"
                   :detailInfo="detailInfo"
                   :columns="columns"
                   :result="result"></titleDetail>
      <theStore :result="result"></theStore>
      <goodsInfo :result="result"></goodsInfo>
      <comment :result="result"></comment>
      <goodsItem :goodsList="recomment"></goodsItem>
    </betterScroll>
    <bottomBar :result="result"
               :goodsId="goodsId"></bottomBar>
    <div @click="jumpTop"
         v-show="showTop"
         class="jump__top">
      <van-icon name="back-top" />
    </div>
  </div>
</template>
<script>
import navtab from './child/navtab.vue'
import detailSwiper from './child/detailSwiper.vue'
import titleDetail from './child/titleDetail.vue'
import theStore from './child/theStore.vue'
import goodsInfo from './child/goodsInfo.vue'
import comment from './child/comment.vue'
import goodsItem from '../../components/goodsItems.vue'
import bottomBar from './child/bottomBar.vue'
import betterScroll from '../../components/betterScroll.vue'
export default {
  name: 'detail',
  data () {
    return {
      resultSuccess: [false, false],
      showTop: false,
      result: {},
      itemInfo: {},
      topImages: [],
      detailInfo: {},
      columns: [],
      recomment: []
    }
  },
  props: {
    goodsId: {
      type: String,
      default () {
        return ''
      }
    }
  },

  computed: {
    eveny () {
      return this.resultSuccess.every((v) => {
        return v === true
      })
    }
  },
  methods: {
    pullingUp () {
      this.$refs.scroll.scroll.refresh()
    },
    jump (target) {
      this.$refs.scroll.scroll.scrollToElement(target, 799)
    },
    scroll (position) {
      if (position.y < -1000) {
        this.showTop = true
      }
      if (position.y > -1000) {
        this.showTop = false
      }
    },
    jumpTop () {
      this.$refs.scroll.scroll.scrollTo(0, 0, 799)
    }
  },
  created () {
    this.$http.getDetail(this.goodsId)
      .then(res => {
        console.log(res)
        const { itemInfo, detailInfo, columns } = res.result
        this.itemInfo = itemInfo
        this.topImages = itemInfo.topImages
        this.detailInfo = detailInfo
        this.columns = columns
        this.result = res.result
        // this.resultSuccess[0] = true
        this.$set(this.resultSuccess, 0, true)
      })
    this.$http.getRecommend()
      .then(res => {
        console.log(res)
        this.recomment = res.data.list
        // this.resultSuccess[1] = true
        this.resultSuccess.splice(1, 1, true)
      })
  },
  components: {
    navtab,
    detailSwiper,
    titleDetail,
    theStore,
    goodsInfo,
    comment,
    goodsItem,
    betterScroll,
    bottomBar
  }
}
</script>
<style lang="scss" >
.scroll1 {
  overflow: hidden;
  position: absolute;
  top: 0.9rem;
  right: 0;
  bottom: 50px;
  left: 0;
}
.detail {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  padding-bottom: 50px;
  background-color: white;
}

.jump__top {
  position: fixed;
  right: 0.3rem;
  bottom: 2rem;

  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  text-align: center;
  background-color: rgb(214, 212, 212);
  i {
    font-size: 0.7rem;
  }
}
</style>
