<template>
  <div style="">
    <div class="goods-info">
      <div class="goods-info__top"></div>
      <p>{{result.skuInfo.title}}</p>
      <div class="goods-info__bottom"></div>
    </div>
    <span style="font-size: 0.35rem;padding-left: 0.35rem">穿着效果 </span>
    <div>
      <img class="detail-img"
           v-for="(v,i) in result.detailInfo.detailImage[0].list"
           :key="i"
           :src="v"
           alt="">
    </div>
    <div class="tables-area">
      <table>
        <thead>
          <tr>
            <th v-for="(v,i) in result.itemParams.rule.tables[0][0]"
                :key="i">{{v}}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(v,i) in rule "
              :key="i">
            <td v-for="(v1,i) in v"
                :key="i">{{v1}}</td>
          </tr>
        </tbody>
      </table>
      <table>
        <tbody>
          <tr v-for="(v,i) in result.itemParams.info.set"
              :key="i">
            <td class="td">{{v.key}}</td>
            <td>{{v.value}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  name: 'goodsInfo',
  props: {
    result: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    rule () {
      return this.result.itemParams.rule.tables[0].filter((v, i) => {
        if (i !== 0) {
          return true
        }
      })
    }
  }
}
</script>
<style lang="scss">
.goods-info {
  padding: 0.5rem 0.35rem 0rem;
  font-size: 0.3rem;
  div {
    width: 2rem;
    height: 1px;
    background-color: #ccc;
  }
  p {
    padding: 0.35rem 0;
  }
  .goods-info__top {
    position: relative;
  }
  .goods-info__top::before {
    content: "";
    position: absolute;
    width: 4px;
    height: 4px;
    background: #000;
  }
  .goods-info__bottom {
    position: relative;
    right: -4.7rem;
  }
  .goods-info__bottom::after {
    content: "";
    position: absolute;
    top: -4px;
    right: 0;
    width: 4px;
    height: 4px;
    background: #000;
  }
}
.detail-img {
  width: 100%;
}
.tables-area {
  padding: 0 0.3rem;
  padding-bottom: 0.4rem;
  border-bottom: 5px solid #ccc;
  font-size: 0.3rem;
  text-align: left;
  table {
    width: 100%;
    border-collapse: collapse;
  }
  thead {
    width: 100%;
  }
  th {
    border-bottom: 1px solid rgb(228, 228, 228);
    width: 25%;
    font-weight: 400;
  }
  td {
    border-bottom: 1px solid rgb(228, 228, 228);
  }
  tr {
    height: 0.85rem;
  }
  .td {
    width: 25%;
  }
  .td + td {
    color: red;
  }
}
</style>
