<template>
  <div>
    <div class="tab__content">
      <div class="tab__item"
           @click="jumpDetail(value.iid)"
           v-for="(value,index) of goodsList"
           :key="index">
        <div class="goods__img">
          <img v-lazy="value.image"
               alt="">
        </div>
        <p>{{value.title}}</p>
        <p class="goods__price"><em>￥{{value.discountPrice}}</em><span>{{value.cfav}}收藏</span></p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'goodsItem',
  props: {
    goodsList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    jumpDetail (goodsId) {
      const target = {
        name: 'detail',
        params: {
          goodsId
        }
      }
      this.$router.push(target)
    }
  },
  mounted () {
    console.log('goodsRender')
  }
}
</script>
<style lang="scss">
::v-deep .van-tab--active {
  color: #ee0a24;
}

.tab__inner {
  font-size: 0.3rem;
}
.tab__content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 15px 5px;
  line-height: 25px;
}
.tab__item {
  width: 46%;
  img {
    width: 100%;
    border-radius: 10px;
  }
  p {
    overflow: hidden;
    font-size: 0.3rem;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
.goods__img {
  height: 5.2rem;
}
.goods__price {
  em {
    font-size: 0.32rem;
    font-weight: 700;
    font-style: normal;
    color: red;
  }
  span {
    margin-left: 5px;
    font-size: 0.24rem;
  }
}
</style>
