<template>
  <div class="title-detail">
    <h3 class="headline">{{itemInfo.title}}</h3>
    <div class="money">
      <em class="money__real">{{itemInfo.price}}</em>
      <em class="money__fake">￥{{itemInfo.lowPrice}}</em>
      <span class="money__discount">{{itemInfo.discountDesc}}</span>
    </div>
    <div class="column">
      <span v-for="(value,index) in columns"
            :key="index">{{value}}</span>
    </div>
    <div class="each-service">
      <span class="each-service__item">
        <span> {{result.shopInfo.services[0].name}}</span>
      </span>
      <span class="each-service__item"
            v-for="(value,index) in shopInfo"
            :key="index">
        <img :src="value.icon"
             alt=""> <span> {{value.name}}</span>
      </span>
    </div>

  </div>
</template>

<script>
export default {
  name: 'titleDetail',
  props: {
    detailInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    itemInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    columns: {
      type: Array,
      default () {
        return []
      }
    },
    result: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    shopInfo () {
      return this.result.shopInfo.services.filter((v, i) => {
        if (i !== 0) {
          return true
        }
      })
    }
  },
  mounted () {
    console.log(111111111111111111)
  }

}
</script>
<style lang="scss" >
.title-detail {
  padding: 0.2rem;
  padding-bottom: 0.4rem;
  border-bottom: 5px solid #ccc;
}
.headline {
  margin: 0;
  font-size: 0.35rem;
  color: #000;
}
.money {
  &__real {
    font-size: 0.55rem;
    font-style: initial;
    color: #ff8198;
  }
  &__fake {
    margin: 0 10px;
    font-size: 0.35rem;
    font-style: normal;
    text-decoration: line-through;
  }
  &__discount {
    display: inline-block;
    padding: 2px 5px;
    border-radius: 15px;
    font-size: 0.3rem;
    color: white;
    background-color: #ff8198;
  }
}
.column {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #ccc;
  font-size: 0.35rem;
}
.each-service {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 0.4rem;
  font-size: 0.15rem;
  &__item {
    span {
      vertical-align: middle;
    }
  }
  img {
    width: 0.3rem;
    vertical-align: middle;
  }
}
</style>
