<template>
  <div class="nav__tab">
    <div class="nav__header">
      <van-row>
        <van-col class="nav__inner"
                 offset="1"
                 span="3">
          <span @click="upperStory"
                class="triangle"></span>
        </van-col>
        <van-col class="nav__header"
                 :class="{'nav__active':colorClass === 0 }"
                 offset="1"
                 @click="jump('.my-swipe',0)"
                 span="3">
          <span>商品</span>
        </van-col>
        <van-col class="nav__header"
                 :class="{'nav__active':colorClass === 1 }"
                 offset="1"
                 @click="jump('.tables-area',1)"
                 span="3">
          <span>参数</span>
        </van-col>
        <van-col class="nav__header"
                 :class="{'nav__active':colorClass === 2 }"
                 offset="1"
                 @click="jump('.comment',2)"
                 span="3">
          <span>评论</span>
        </van-col>
        <van-col class="nav__header"
                 :class="{'nav__active':colorClass === 3 }"
                 offset="1"
                 @click="jump('.tab__content',3)"
                 span="3">
          <span>推荐</span>
        </van-col>
      </van-row>
    </div>

  </div>
</template>
<script>
export default {
  name: 'navtab',
  data () {
    return {
      flag: 0,
      colorClass: 0
    }
  },
  methods: {
    updateColor () {
      this.colorClass = this.flag
    },
    jump (target, flag) {
      this.flag = flag
      this.$emit('jump', target)
    },
    upperStory () {
      this.$router.back()
    }

  },
  created () {
    this.$eventBus.$on('tabUpdateColor', this.updateColor)
  }
}
</script>
<style lang="scss">
.nav {
  &__tab {
    font-size: 0.27rem;
  }
  &__header {
    height: 0.9rem;
    line-height: 0.9rem;
  }

  &__active {
    color: red;
  }
}
.triangle {
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  margin-left: 0.2rem;
  border-left: 1px solid #000;
  border-top: 1px solid #000;
  transform: rotate(-45deg) skew(3deg, 3deg);
}
</style>
