<template>
  <div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o"
                             text="客服"
                             color="#ee0a24" />
      <van-goods-action-icon icon="cart-o"
                             text="购物车" />
      <van-goods-action-icon icon="star"
                             text="已收藏"
                             color="#ff5000" />
      <van-goods-action-button type="warning"
                               @click="addToCart"
                               text="加入购物车" />
      <van-goods-action-button type="danger"
                               text="立即购买" />
    </van-goods-action>
  </div>
</template>
<script>
import { Toast } from 'vant'
import { mapMutations } from 'vuex'
export default {
  name: 'bottomBar',
  props: {
    result: {
      type: Object,
      default () {
        return {}
      }
    },
    goodsId: {
      type: String,
      default () {
        return ''
      }
    }
  },

  methods: {
    // ...mapMutations(['ADD_GOODS_DATA']),
    ...mapMutations({
      addGoods: 'ADD_GOODS_DATA'
    }),
    onClickIcon () {
    },
    addToCart () {
      Toast.success('成功文案')
      this.addGoods({
        goodsId: this.goodsId,
        title: this.result.detailInfo.desc,
        detail: this.result.itemInfo.desc,
        Price: this.result.itemInfo.lowNowPrice,
        topImages: this.result.itemInfo.topImages[0],
        num: 1
      })
    }
  },
  mounted () {
    console.log(this.result)
  },
  created () {

  }
}
</script>
